.region-sidebar-left {
  .block {
    margin-bottom: 40px;
    h2 {
      font-weight: normal;
      line-height: 3.2rem;
      font-size: 2.4rem;
      color: #333;
      margin-bottom: 12px;
    }
  }
  .sidebar-menu-block {
    background: #f5f5f5;
    line-height: 2.6rem;
    padding: 20px 0;
    .menu {
      .menu__item {
        cursor: pointer;
        .menu__link {
          padding: 10px 30px 10px 30px;
          text-decoration: none;
          color: #4a4a4a;
          &.is-active, &:hover, &:focus {
            color: #005794 !important;
          }
        }
        &.dropdown-item {
          .menu__link {
            padding-right: 0;
            width: calc(100% - 45px);
          }
        }
        &:hover, &:focus {
          background: none;
          > .menu__link, > .fa {
            color: #4a4a4a;
            cursor: pointer;
          }
        }
        .fa {
          background: none !important;
          height: 47px;
          width: 35px;
          padding-right: 10px;
          box-sizing: content-box;
          -webkit-box-sizing: content-box;
          font-weight: bold;
          &:hover, &:focus {
            outline: none;
            color: #005794 !important;
            &:before {
              text-decoration: none;
            }
          }
        }
        ul {
          .menu__item {
            .menu__link {
              padding: 6px 0 6px 40px;
              font-size: 1.6rem;
              line-height: 2.1rem;
            }
            ul {
              .menu__item {
                .menu__link {
                  padding: 6px 0 6px 50px;
                }
              }
            }
            .fa {
              height: 34px;
            }
          }
        }
      }
    }
  }
  .block-ctools-block {
    .paragraph--type--quick-link {
      margin-top: 0;
      a {
        padding-left: 0;
        padding-right: 0;
        text-decoration: none;
        font-size: 1.6rem;
        border-top: 2px solid #e9e9e9;
      }
    }
    .paragraph--type--wysiwyg {
      margin-top: 40px;
      h4 {
        font-weight: normal;
        line-height: 3.2rem;
        font-size: 2.4rem;
        color: #333;
        margin-bottom: 12px;
      }
      p {
        font-size: 1.6rem;
        line-height: 1.6;
      }
    }
  }


}